:root {
  --podkit-font-color: #000;
  --podkit-font-highlight-color: hsl(8, 100%, 56%);
  --podkit-bg-color: hsl(8, 100%, 86%);
  --podkit-bg-mid-color: hsl(8, 100%, 79%);
  --podkit-bg-shadow-color: hsl(8, 100%, 76%);
}

.is-style-blue {
  --podkit-font-highlight-color: hsl(209, 100%, 56%);
  --podkit-bg-color: hsl(209, 100%, 86%);
  --podkit-bg-mid-color: hsl(209, 100%, 79%);
  --podkit-bg-shadow-color: hsl(209, 100%, 76%);
}

.is-style-yellow {
  --podkit-font-highlight-color: hsl(49.7, 93.5%, 56%);
  --podkit-bg-color: hsl(49.7, 93.5%, 86%);
  --podkit-bg-mid-color: hsl(49.7, 93.5%, 79%);
  --podkit-bg-shadow-color: hsl(49.7, 93.5%, 76%);
}

.podkit-block {
  display: grid;
  grid-template-columns: 20% 1fr;
  padding: 1em;
  font-family: sans-serif;
  background-color: var(--podkit-bg-color);
  box-shadow: 5px 5px 0 var(--podkit-bg-mid-color),
    10px 10px 0 var(--podkit-bg-shadow-color);
}

.podkit-logo,
.podkit-img {
  grid-row: span 2;
  overflow: hidden;
}

.podkit-logo img,
.podkit-img img {
  display: block;
}

.podkit-info {
  margin-left: 1em;
  color: var(--podkit-font-color);
}

.podkit-info > * {
  margin: 0;
  font-weight: lighter;
}

.podkit-nameplate,
.podkit-cta {
  font-size: 80%;
}

.podkit-nameplate {
  margin-bottom: -0.25em;
}

.podkit-title {
  font-size: 200%;
}

.podkit-description {
  grid-column: span 2;
  margin-top: 1em;
  line-height: 1.3em;
}

.podkit-description p {
  margin: 0;
  padding-bottom: 1em;
  font-weight: lighter;
}

.podkit-description p:last-of-type {
  padding-bottom: 0;
}

.podkit-cta {
  grid-column: span 2;
  display: flex;
  padding-top: 1em;
  font-weight: normal;
}

.podkit-cta a::before {
  display: inline-block;
  margin: -3px 0.75em -3px -3px;
  padding: 0.25em 0.85em 0.1em 1.25em;
  content: "â–¶";
  font-size: 0.8em;
  line-height: 2.6em;
  background: white;
  border-radius: 50%;
  border: 3px solid var(--podkit-bg-color);
}

.podkit-cta a,
.entry .entry-content .podkit-cta a {
  margin-left: -3px;
  padding-right: 1.5em;
  text-decoration: none;
  color: var(--podkit-font-highlight-color);
  background: white;
  border-radius: 1.5em;
  border: 3px solid var(--podkit-bg-color);
}

.podkit-cta a:focus::before,
.podkit-cta a:hover::before {
  color: white;
  background-color: var(--podkit-font-highlight-color);
}

.podkit-block.alignwide .podkit-description,
.podkit-block.alignfull .podkit-description {
  grid-column: 2;
  margin-left: 1em;
}

.podkit-block.alignfull {
  padding: 3em;
  box-shadow: none;
}

// Flip Box

:root {
  --time: 0.707s;
}

.cards {
  display: grid;
}

.card {
  perspective: 40rem;
  z-index: 0;
}

.card-body {
  transform-style: preserve-3d;
  transition: var(--time) transform;

  .card:hover & {
    transform: rotateX(-180deg);
    z-index: 1;
  }
}

.card-front,
.card-back {
  backface-visibility: hidden;
  min-width: 100%;
}

.card-back {
  position: absolute;
  bottom: 0;
  left: 0;
  transform: rotateX(-180deg);
}

/* Make it Pretty */

body {
  line-height: 1.3;
  font-family: sans-serif;
}

.cards {
  grid-gap: 1.5rem;
  padding: 3rem;
  // background: whitesmoke;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
}

.card {
  display: flex;
  transition: transform calc(var(--time) / 4);
  text-decoration: none;
  color: inherit;

  &:active {
    transform: scale(0.975);
  }
}

.card-body {
  border-radius: 0.25rem;
  flex: 1;
}

.card-front,
.card-back {
  // @extend .shadow;
  display: flex;
  align-items: center;
  background-color: white;
  box-sizing: border-box;
  padding: 1.5rem;
  border-radius: 0.25rem;
}

.card-front,
.card-back {
  font-size: 1.5rem;
}

.shadow {
  box-shadow: 0 -1px 1px rgba(0, 0, 0, 0.04), 0 2px 2px rgba(0, 0, 0, 0.04),
    0 4px 4px rgba(0, 0, 0, 0.04), 0 8px 8px rgba(0, 0, 0, 0.04),
    0 16px 16px rgba(0, 0, 0, 0.04);
}

// Button

.wpb_button {
  display: inline-block;
  background-color: #000000;
  color: #ffffff;
}
.wpb_button_container {
  padding: 16px;
}

// Fade (Background)
.wpb_fade {
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  overflow: hidden;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: color, background-color;
  transition-property: color, background-color;

  &:hover,
  &:focus,
  &:active {
    background-color: #2098d1;
    color: white;
  }
}

/* Back Pulse */
@-webkit-keyframes hvr-back-pulse {
  50% {
    background-color: rgba(32, 152, 209, 0.75);
  }
}
@keyframes hvr-back-pulse {
  50% {
    background-color: rgba(32, 152, 209, 0.75);
  }
}

.wpb_pulse {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  overflow: hidden;
  -webkit-transition-duration: 0.5s;
  transition-duration: 0.5s;
  -webkit-transition-property: color, background-color;
  transition-property: color, background-color;

  &:hover,
  &:focus,
  &:active {
    -webkit-animation-name: hvr-back-pulse;
    animation-name: hvr-back-pulse;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-delay: 0.5s;
    animation-delay: 0.5s;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    background-color: #2098d1;
    background-color: #2098d1;
    color: white;
  }
}

/* Sweep To Right */
.wpb_sweep_right {
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  -webkit-transition-property: color;
  transition-property: color;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #2098d1;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: 0 50%;
    transform-origin: 0 50%;
    -webkit-transition-property: transform;
    transition-property: transform;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
  }

  &:hover,
  &:focus,
  &:active {
    color: white;
  }

  &:hover::before,
  &:focus::before,
  &:active::before {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
}

/* Sweep To Left */
.wpb_sweep_left {
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  -webkit-transition-property: color;
  transition-property: color;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;

  &:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #2098d1;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: 100% 50%;
    transform-origin: 100% 50%;
    -webkit-transition-property: transform;
    transition-property: transform;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
  }

  &:hover,
  &:focus,
  &:active {
    color: white;
  }

  &:hover::before,
  &:focus::before,
  &:active::before {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
}
